


























import Vue from "vue";
import GoBack from "@/components/util/GoBack.vue";
interface IData {
  before: number;
  after: number;
  dragonjade: number;
  attackPercent: number;
  isActive:boolean;
}

export default Vue.extend({
  components: {
        GoBack,
    },
  data(): IData {
    return {
      before: 0,
      after: 0,
      dragonjade: 110000,
      attackPercent: 0,
      isActive:false
    };
  },
  watch: {
    before(val) {
      this.isActive = false;
      if (val > 1000000 && this.after>1000000) {
        this.cacl();
      }else{
        this.attackPercent = 0
      }
    },
    after(val) {
      this.isActive = false;
      if (val > 1000000 && this.before>1000000) {
        this.cacl();
      }else{
        this.attackPercent = 0
      }
    },
    dragonjade() {
      this.cacl();
    },
  },
  methods: {
    cacl() {
      this.attackPercent = Math.round(
        ((this.before - this.after) / this.dragonjade - 1) * 100
      );
      this.isActive = true;
    },
  },
});
